<template>
    <van-swipe :autoplay="3000" class="my-swipe">
        <van-swipe-item v-for="(image, index) in banners" :key="index">
            <img v-lazy="image"/>
        </van-swipe-item>
    </van-swipe>
</template>
<script>
    export default{
        data(){
            return{
                banners:[],
                src:['../../static/images/lunbo/err.jpg','../../static/images/lunbo/err.jpg','../../static/images/lunbo/err.jpg','../../static/images/lunbo/err.jpg','../../static/images/lunbo/err.jpg','../../static/images/lunbo/err.jpg','../../static/images/lunbo/err.jpg'],
            }
        },
        props:['tabs'],
        beforeCreate(){
           this.$axios.get('/static/data.json').then((res)=>{
           if( res.status == 200 ) {
               const data = res.data.home.lunbo;
               switch(this.tabs.content){
                    case '今日推荐':
                       this.banners = data.home;
                    break;
                    case '美妆':
                       this.banners = data.meizhuang;
                    break;
                    case '家电':
                       this.banners = data.jiadian;
                    break;
                    case '家居':
                       this.banners = data.jiaju;
                    break;
                    case '国际':
                       this.banners = data.guoji;
                    break;
                    case '生活':
                       this.banners = data.shenghuo;
                    break;
                    default:
                        this.banners = this.src;
               }
            }else{
                this.banners = this.src;
            }
      })
    }
}
</script>

<style lang="less" scoped>
    .my-swipe{
        img{
            width:100%;
            height:130px;
        }
    }
</style>
