export default {
    'page':({commit},payload)=>{
        commit('page',payload);
    },
    'cartGoods':({commit},payload)=>{
        commit('cartGoods',payload);
    },
    'bFoot':({commit},payload)=>commit('bFoot',payload),
    'bHead':({commit},payload)=>commit('bHead',payload),
    'isLogin':({commit},payload)=>commit('isLogin',payload),
    // 'allGoods':({commit,state},payload)=>{
    //     var count = 0;
    //     goods.forEach(item => {
    //         count += item.num;
    //     });
    //     payload = count;
    //     console.log(payload)
    //     commit('allGoods',payload);
    // }
    changeItem:({commit,state},payload)=>{
        // [...state.buycar]
        // {....state}
        // console.log(payload);
        state.cartGoods.forEach((item,index)=>{
            console.log(item.no,payload._id)
          if(item.no==payload._id){
            //   console.log()
            item.num += payload.num;
          }
        });
        //如果是对象，需要复制一份，不然state.buycar指向的是复制后state修改之前的引用
        commit('changeItem',[...state.cartGoods])
        // commit(types.CHANGE_ITEM,state)  ×
        console.log(12);
      },
    allPrice:({commit,state},payload)=>{
        state.cartGoods.forEach(item => {
            if(item.no == payload && item.selected){
                state.Price += item.num * item.price;
            }else{
                state.Price -= item.num * item.price;
            }
        })
        commit('allPrice',['...state.Price'])
    }
};