let getters = {
    page:(state)=>state.page,
    cartGoods:(state)=>state.cartGoods,
    getAllCount(state){
        var c = 0;
        // console.log(state.cartGoods);
        if(state.cartGoods != []){
            state.cartGoods.forEach(item => {
                c += item.num 
            });
        }else{
            c = 0;
        }
        return c;
    },
    'bFoot':(state)=>state.bFoot,
    'Price':(state)=>state.Price,
    'bHead':(state)=>state.bHead,
    'isLogin':(state)=>state.isLogin,
    areSelected(state){
        var obj = {};
        state.cartGoods.forEach(item=>{
            obj[item.no] = item.selected;
        })
        return obj;
    },
    getItem(state){
        var obj = {};
        state.cartGoods.forEach(item=>{
            obj[item.no] = item.num;
        })
        return obj;
    },
    // allPrice(state){
    //     let price = 0;
    //     state.cartGoods.forEach(item => {
    //         if(item.selected == true){
    //             price += item.price * item.num;
    //         }
    //     })
    //     return price;
    // }
}
export default getters; 