<template>
   <!-- 登录注册 组件 -->
  <div class="login">
            <div class="welcome"><img src="/static/images/welcome.png"></div>
                <div class="login-form">
                    <div class="login-inp"><label>用户名</label><input type="text" placeholder="请输入用户名" v-model="username"/></div>
                    <div class="login-inp"><label>密码</label><input type="password" placeholder="请输入密码" v-model="password"/></div>
                    <div class="login-inp"><a href="#" @click="login()">{{btnName}}</a></div>
                </div>
            <div class="login-txt"><a href="javaScript:;" @click="redirects('/register')">立即注册</a>|<a href="#" @click="tip()">忘记密码？</a>
            </div>
        <div style="text-align:center;">
        </div>
  </div>

</template>

<script>
// import { mapState,mapActions,mapGetters } from 'vuex';
import { Toast } from 'vant';
// import { Dialog } from'vant';
import axios from 'axios';
export default {
  name: 'login',
  components:{
  },
  data() {
    return {
       username:null,
       password:null,
       btnName:'立即登录',
    }
  },
  computed: {
  },
  methods: {
    //用户名和密码验证
    rules(v,choose){
        if(choose == 'user') {
             let username = v.trim();
             let uPattern = /^[a-zA-Z0-9_]{4,12}$/g;

             if(username.split('').length > 12 ) { return false;}

             if(!uPattern.test(username)) {
                 Toast('请输入4到16位(字母,数字,下划线)')
             }
            //  console.log(username,v.trim());
        }else if(choose == 'pass') {
            let password = v.trim();
            //最少6位，包括至少1个大写字母，1个小写字母，1个数字，1个特殊字符
            let pPattern = /^.*(?=.{6,})(?=.*\d)(?=.*[A-Z])(?=.*[a-z])(?=.*[!@#$%^&*? ]).*$/;
             if(!pPattern.test(password)) {
                 Toast('密码强度较弱o(╥﹏╥)o');
             }else{
                 Toast('密码强度 安全^_^');
             }
        } 
    },
    login(){
        if(this.username == null || this.password == null ) {
            Toast('用户名和密码不能为空 o(╥﹏╥)o')
        }else{
            let user = this.username;
            let password = this.password;
            axios({
                url:'/api/login',
                method:'post',
                data:{
                    user,
                    password
                }
            }).then((res) => {
                if(res.data.err == 0){
                    this.$router.push('/me');
                }else{
                    Toast(res.data.err);
                }                
            })
        }
    },
    redirects(url) {
      this.$router.push(url);
        if(this.$route.name == 'login') {
            this.btnName = '立即登录';
        }else if (this.$route.name == 'register') {
            this.btnName = '立即注册';
        }
    },
    tip() {
        Toast('该功能正在维护');
    },
  },
  watch: {
  },
  directives: {
  },
  beforeCreate() {

  },
  created() { 
  }
}
</script>

<style lang="less" scoped>
  @import url('../assets/css/login.less');
</style>