let mutations = {
    'page':(state,payload)=>state.page = payload,
    'cartGoods':(state,payload)=>state.cartGoods = payload,
    // 'allGoods':(state,payload)=>{state.allGoods = payload}
    'bFoot':(state,payload)=>state.bFoot = payload,
    'bHead':(state,payload)=>state.bHead = payload,
    'isLogin':(state,payload)=>state.isLogin = payload,
    'changeItem':(state,payload)=>{
        state.cartGoods = payload;
        localStorage.setItem("mobile_goods",JSON.stringify(payload));
    },
    'allPrice':(state,payload)=>{
        state.Price = payload;
    }
}
export default mutations;