<template>
   <div class="home">
     <!-- 搜索区 -->
    <!-- 标签区域 -->
    <van-row>
      <van-col span="24">
        <van-tabs v-model="active" swipeable v-tab>
          <van-tab v-for="index in 3" 
          :title="title[index]" 
          :key="index.id" 
          class="tab"
          >
          <van-pull-refresh v-model="isLoading" @refresh="onRefresh">
            <!-- 动态版块 -->
            <div v-if="title[index]== '动态'" class="contain">
        
                  <van-row class="col-2">
                    <!-- 通知 -->
             </van-row>
              <!-- 活动版块 -->
                <news :tabs="title[index]" />
            </div>
            <!-- 时尚版块 -->
            <div v-if="title[index]== '热门'" class="contain">
              <news :tabs="title[index]" />
            </div>
            <!-- 美妆版块 -->
            <div v-if="title[index]== '发现'" class="contain">
              <news :tabs="title[index]" />
            </div>
             </van-pull-refresh>   
          </van-tab>
        </van-tabs>
      </van-col>
    </van-row>

    <!-- tabBar -->
       <van-tabbar class="footer">
        <van-tabbar-item  icon="wap-home" @click="redirects('/')">首页</van-tabbar-item>
        <van-tabbar-item icon="chat-o" dot @click="redirects('/community')">社区</van-tabbar-item>
        <van-tabbar-item icon="shopping-cart" info="5"  @click="redirects('/cart')">购物车</van-tabbar-item>
        <van-tabbar-item icon="contact" info="2" @click="redirects('/me')">我的</van-tabbar-item>
      </van-tabbar>
   </div>
</template>
<script>
import News from './news';

export default {
  name: 'community',
  components:{
    News
  },
  data() {
    return {
        title:['','动态','热门','发现'],
        tabarActive:1,
        value:null,
        active:0,
        path:'../../static/images/',
        imageList:[],
        activeTitle:null,
        days:null,
        disabled:false,
        broadcast:null,
        show:true,
        count: 0,
        isLoading: false
    }
  },
  computed: {
    // ...mapState({
    //   title: state => state.community.tab.title,
    //   icon: state => state.home.badge.icon,
    //   bageTitle: state => state.home.badge.title,
    //   actives: state => state.active.home.title,
    //   src : state => state.home.lunbo.src,
    //   activeTitle: state => state.active.home.activeTitle,
    //   days: state => state.active.home.days,
    //   broadcast: state => state.home.broadcast,
    //   shop_info: state => state.home.shop_info,
    //   my_info: state => state.home.my_info,
    //   show: state => state.home.show,
    // }),
    // ...mapGetters(['bc_notshow','search_show']),
  },
  methods: {
    
    search() {
      this.$router.push('/search');
    },
    onRefresh() {
      setTimeout(() => {
        this.$toast('刷新成功');
        this.isLoading = false;
        this.count++;
      }, 500);
    },
     // 瀑布流方法
     loadMore() {
      this.disabled = true;
       setTimeout(() => {
         for (let i = 0; i < 5; i++) {
            this.imageList.push(this.imageList[i]);
            this.activeTitle.push(this.activeTitle[i]);
            this.days.push(this.days[i]); 
        }
        this.disabled = false;
       }, 200);
    },
    redirects(url) {
      this.$router.push(url);
    },
  },
  watch: {

  },
  directives: {
    //瀑布流
    // WaterfallLower: List('lower'),
    tab:{
      inserted(el) {
        //绑定tab样式
        const tabs = el.childNodes[0].childNodes[0].children[0];
        tabs.style.backgroundColor = '#d34ba8';
      }
    },
    //tabBar 消息通知指令
    infos:{
      inserted(el,obj) {
         const info = el.childNodes[0].childNodes[1];
         info.innerText = obj.value;
      }
    }

  },
  beforeCreate() {
     this.$axios.get('./static/data.json').then((res)=>{
        if( res.status == 200 ) {
              const data = res.data.home;
              this.broadcast = data.broadcast;
          } else {
            this.imageList = this.src;
            this.broadcast = "暂无消息~~QAQ~"
          }
          },(err)=>{
            if(err){
              this.imageList = this.src;
              this.broadcast = "暂无消息~~QAQ~"
          }
    })
  },
  created() {

  }
}
</script>

<style lang="less" scoped>
@import url('../assets/css/home.less');
.head{
  margin-left:10px;
}
.home{
  @fonts:16px;
     .row-1{
         height: 35px; 
         margin:8px 0;
         font-size: 15px;
         width:375px;
     }
     .contain{
         overflow: scroll;
         height: 600px;
     }
     .cols a{
         display: block;
         width: 60px;
         height: 35px;
         line-height: 35px;
         text-align: center;
     }
     .classfic {
         display: block;
         width: 60px;
         height: 35px;
         line-height: 35px;
         margin-left: -30px;
         font-size: 20px;
     }
     .col-2{
         background-color: white;
         h4{
             font-size: @fonts;
             margin-top: 30px;
             color: black;
             font-weight: normal;
         }
     }
     .row-2{
        margin-top: 20px;
        height: 70px;
        img{
            display:block;
            margin:0 auto;
        }
        span{
            display: block;
            color:#555;
            font-size: 12px;
            margin-bottom: 10px;
            text-align:center;
        }
     }
}
.my-swipe{
    img{
        width:100%;
        height:130px;
    }
}
.footer{
    width:375px;
}
</style>
