<template>
 <!-- 购买信息组件  大部分都是一样的 所以数据方面 我就用同一个 作为展示作用-->
  <div class="home">
        <van-row class="col-2">
    <!--导航 -->
        <van-col span='24' class="title">
            <div class="back">
                <a href="javaScript:;" @click.prevent="search_shows">&lt;</a>
                <span>{{actives[2]}}</span>
            </div>
        <div class="preImg" @click="ImagePreviews()">
            <img v-lazy="goodsImg" name="adapter" @click="ImagePreviews()"/>
        </div>  
     </van-col>
    </van-row>

    <van-row class="col-3">
        <van-col span="24" class="lazy-bottom">
             <h4><i>￥{{price}}</i><s>￥{{original_price}}</s><b>{{discount}}折</b></h4> 
         </van-col>

     <van-col span="24" class="lazy-left">
           <span>{{this.des}}</span>
     </van-col>
     <hr/>
     <van-col class="empty"></van-col>
    </van-row>

    <!-- 优惠券单元格 -->
    <van-coupon-cell
    :coupons="coupons"
    :chosen-coupon="chosenCoupon"
    @click="showList = true"
    class="lists"
    />

    <!-- 优惠券列表 -->
    <van-popup v-model="showList" position="bottom">
    <van-coupon-list
        :coupons="coupons"
        :chosen-coupon="chosenCoupon"
        :disabled-coupons="disabledCoupons"
        @change="onChange"
        @exchange="onExchange"
    />
    </van-popup>
    <!-- <order class="order" /> -->
     <van-goods-action>
        <van-goods-action-icon
            icon="chat-o"
            text="客服"
        />
        <van-goods-action-icon
            :info="$store.getters.getAllCount"
            icon="cart-o"
            text="购物车"
            @click="redirecte('/cart')"
        />
        <van-goods-action-button
            type="warning"
            text="加入购物车"
            @click="addCart"
        />
        <van-goods-action-button
            type="danger"
            text="立即购买"
        />
    </van-goods-action>
  </div>
</template>
<script>
    import { mapState,mapActions,mapGetters } from 'vuex';
    import { ImagePreview } from 'vant';
    import { Toast } from 'vant';
        // import Order from './orderList';
        //obj 优惠券
        const coupon = {
            available: 1,
            discount: 0,
            denominations: 150,
            origin_condition: 0,
            reason: '',
            value: 150,
            name: '满498减100',
            start_at: 1489104000,
            end_at: 1514592000
        };
        export default {
        name: 'buyInfo',
        data() {
            return {
            actives:['今日特卖 · 每天早10点 晚8点上新','运动休闲男女专场','剩2天20小时'],
            preImgs:[],
            chosenCoupon: -1,
            coupons: [coupon],
            disabledCoupons: [coupon],
            showList:null,
            oShow:false,
            goodsImg:'',
            price:'',
            original_price:'',
            des:'',
            discount:'',
            pageItem:'',
            data:{},
            goodsId:'',
            goods:[],
            no:null
        }
        },
        props:{
        
        },
        components:{
            // Order,
        },
        mounted(){
            this.pageItem = this.$store.state.page;
        },
        computed: {
            ...mapGetters(['page']),
        },
        methods: {
            addCart(){
                this.goods = localStorage.getItem("mobile_goods");
                if(this.goods){
                    this.goods = JSON.parse(this.goods);
                    var firstAdd = 0;
                    for(var i = 0;i<this.goods.length;i++){
                        if(this.goods[i]._id == this.goodsId){
                            // goods[i].num += parseInt($(".num").html());
                            this.goods[i].num ++;
                            firstAdd = 1; 
                        }
                    }
                    if(firstAdd == 0){
                            this.goods.push({
                            _id:this.goodsId,
                            price:this.price,
                            no:this.no,
                            selected:true,
                            num:1
                        });
                    }
                }else{
                    this.goods = [{
                        _id:this.goodsId,
                        price:this.price,
                        no:this.no,
                        selected:true,
                        num:1
                    }];
                }
                localStorage.setItem("mobile_goods",JSON.stringify(this.goods));
                this.$store.dispatch("cartGoods",this.goods);
        },
            // ...mapActions(['orderShows']),
            redirecte(path){
                this.$router.push(path);
            },
            search_shows(){
                this.$router.back(-1);
            },
            //商品预览
            ImagePreviews() {
                ImagePreview(this.preImgs);
            },
            //优惠券
            onChange(index) {
                this.showList = false;
                this.chosenCoupon = index;
            },
            onExchange(code) {
                this.coupons.push(coupon);
            },
            onClickMiniBtn() {
                Toast('系统繁忙 请稍后再试');
            },
            onClickBigBtn() {
                Toast('请选择商品规格');
                this.orderShows();
            // console.log(this.orderShow)
            },
            onClickBigBtns() {
                this.orderShows();
                Toast('加入成功');
            }
        },
        watch: {

        },
        directives: {
        
        },
        beforeCreate() {
        },
        created() {
            if(this.buyImg == 'undefined') {
            this.buyImg = 'https://a4.vimage1.com/upload/merchandise/pdc/544/548/464510208477548544/0/880555-001-5_218x274_70.jpg';
            }
            this.goodsId = this.$route.params.id; 
            this.$axios.get(`/api/getGoods?page=${this.$store.state.page}&goodsId=${this.goodsId}`).then((res)=>{
                // console.log(res);
                if(res.data.err == 0){
                    this.data = res.data.data[0];
                    this.goodsImg=this.data.imgList;
                    this.price=this.data.limit_price;
                    this.original_price=this.data.original_price;
                    this.des=this.data.description;
                    this.discount=this.data.discount;
                    this.no = this.data.id;
                    console.log(this.no);
                }
            })
        }
        }
</script>

<style lang="less" scoped>
.back{
    overflow:hidden;
    font:16px/2 "";
    a{float:left;display:block;height:32px;width:32px;border-radius:50%;background:#000;
    opacity:0.5;
    text-align:center;font:18px/32px "微软雅黑";color:#fff;margin-left:10px;}
    span{float:right;display:block;line-height:32px;margin-right:20px;color:red;}
}
.preImg{
    img{
        width:80%;
        display:block;
        margin:0 auto;
    }
}
.lazy-bottom{
    i{
        color:purple;
        margin-left:20px;
    }
    s{
        color:red;
        margin-left:10px;
    }
    b{
        margin-left:180px;
        background: #000;
        color: white;
        padding: 1px 2px 1px 2px;
        font-size: 10px;
        text-align: center;
        display: inline-block;
    }
}
.lazy-left{
    span{
        margin-left:20px;
    }
}
</style>

