<template>
<!-- 登录注册 组件 -->
    <div class="login">
        <div class="welcome"><img src="/static/images/welcome.png"></div>
        <div class="me-img">
            <!-- :after-read="onRead" -->
            <!-- <div v-show="img == ''" class="uploadImg">上传头像</div> -->
            <img v-show="img != ''" v-lazy="img" name="adapter" @click.stop="redirect('#')" />
            <van-uploader  :after-read="onRead" accept="image/gif, image/jpeg ,image/png" multiple ref="image">
                <van-icon name="photograph" style="margin:10px 0;"/>
            </van-uploader>
        </div>
        <div class="login-form">
            <div class="login-inp"><label>用户名</label><input type="text" placeholder="请输入用户名" v-model="username"/></div>
            <div class="login-inp"><label>昵称</label><input type="text" placeholder="请输入昵称" v-model="nikename"/></div>
            <div class="login-inp"><label>密码</label><input type="password" placeholder="请输入密码" v-model="password"/></div>
            <div class="login-inp"><a href="javaScript:;" @click="register()">{{btnName}}</a></div>
        </div>
        <div class="login-txt"><a href="javaScript:;" @click="redirects('/login')">去登录</a>|<a href="#" @click="tip()">忘记密码？</a>
        </div>
    <div style="text-align:center;">
    </div>
</div>
</template>
<script>
// import { mapState,mapActions,mapGetters } from 'vuex';
import { Toast } from 'vant';
// import { Dialog } from'vant';
export default {
name: 'login',
components:{
},
data() {
return {
    username:null,
    password:null,
    nikename:null,
    btnName:'立即注册',
    img:'static/images/chushi.png',
    // img:''
    f:null
}
},
computed: {

},
methods: {
    onRead(file) {
    //   console.log(file);
      this.f = file; 
      this.img = file.content;
    //   console.log(this.f)
      Toast('选择头像成功');
    },
//用户名和密码验证
    rules(v,choose){
        if(choose == 'user'){
            let username = v.trim();
            let uPattern = /^[a-zA-Z0-9_]{4,12}$/g;
            if(username.split('').length > 12){return false;}
            if(!uPattern.test(username)) {
                Toast('请输入4到16位(字母,数字,下划线)')
            }
            // console.log(username,v.trim());
        }else if(choose == 'pass'){
            let password = v.trim();
            //最少6位，包括至少1个大写字母，1个小写字母，1个数字，1个特殊字符
            let pPattern = /^.*(?=.{6,})(?=.*\d)(?=.*[A-Z])(?=.*[a-z])(?=.*[!@#$%^&*? ]).*$/;
                if(!pPattern.test(password)){
                    Toast('密码强度较弱o(╥﹏╥)o');
                }else{
                    Toast('密码强度 安全^_^');
                }
        } 
    },
    register(){
        if(this.username == null || this.password == null ) {
            Toast('用户名和密码不能为空 o(╥﹏╥)o')
        }else if(this.nikename == null){
            Toast('请输入昵称');
        }else{
            let formData = new FormData();
            formData.append('user',this.username);
            formData.append('nikename',this.nikename);
            formData.append('password',this.password);
            formData.append('icon',this.f.file);
            this.$axios({
                url:'/api/reg',
                method:'post',
                data:formData,
                }
            ).then((res) => {
                if( res.data.err == 0 ) {
                    // Toast('注册成功');
                    this.$router.push('/login');
                }else{
                    Toast(res.data.msg);
                }
            })
        }
},
redirects(url) {
    this.$router.push(url);
    if(this.$route.name == 'login'){
        this.btnName = '立即登录';
    }else if (this.$route.name == 'register') {
        this.btnName = '立即注册';
    }
},
tip() {
    Toast('该功能正在维护');
},

},
watch: {

},
directives: {

},
beforeCreate() {

},
created() { 
}
}
</script>

<style lang="less" scoped>
    @import url('../assets/css/login.less');
    .me-img{
        margin-top:-50px;
        text-align:center;
          img{
              width:80px;
              height: 80px;
              border-radius: 50px;
              background-size: 200%;
              background-position: 50% 50%;
              display:block;
              margin:0 auto;
          }
        }
    .uploadImg{
        margin:0 auto;
        width:80px;
        height:80px;
        border-radius:50%;
        background:#fff;
        font:16px/80px "微软雅黑";
        color:#000;
    }
    input.multiple{
    line-height:45px;
    }
    .van-icon{
        margin:0;
    }
</style>
