<template>
   <!-- 购物车组件 -->
  <div class="home">
     <!-- 搜索区 -->
<!-- <van-pull-refresh v-model="isLoading" @refresh="onRefresh" class="cartBox"> -->
    <div v-for="item in goodsList" :key="item.id" class="shop-cart">
        <!-- 标签区域 -->
        <van-row class="shop-col">
            <van-col span="10" class="check" offset="1">
                <!-- <van-checkbox v-model="$store.getters.areSelected[item.id]" @change="isChecked">{{item.shops}}</van-checkbox> -->
                <input type="checkbox" v-model="$store.getters.areSelected[item.id]" @change="isChecked(item.id)">
            </van-col>
            <van-col span="4" offset="7">
                <span class="edit" @click="showList = true">领券&nbsp;|&nbsp;编辑</span>
            </van-col>
        </van-row>
        <van-swipe-cell>
          <template slot="left">
            <van-button square type="primary" text="选择" />
          </template>
  <!-- <van-cell :border="false" title="单元格" value="内容" /> -->
            <van-card
              :price="item.limit_price"
              :desc="item.description"  
              :title="item.title"
              :thumb="item.imgList"
            >
              <div slot="footer" class="menu">
                <van-button size="mini" @click="changeItem({_id:item.id,num:-1})">-</van-button>
                <input type="number" :value="$store.getters.getItem[item.id]" min="1" readonly ref="count"> 
                <van-button size="mini" @click="changeItem({_id:item.id,num:1})">+</van-button>
              </div>
            </van-card>
        <template slot="right">
          <van-button square type="danger" text="删除" />
          <van-button square type="primary" text="收藏"/>
        </template>
      </van-swipe-cell>
    </div>
    <van-submit-bar
        :price="1210"
        button-text="提交订单"
      >
    <van-checkbox>全选</van-checkbox>
    <span slot="tip">
      你的收货地址不支持同城送, <span>修改地址</span>
    </span>
  </van-submit-bar>
  </div>
</template>
<script>
  import { mapGetters,mapActions } from 'vuex';
// import { Toast } from 'vant';
  export default {
    name: 'shoppingCart',
    components:{
    },
    data(){
      return {
        radio: '1',
        imgUrl:"https://a4.vimage1.com/upload/merchandise/pdc/544/548/464510208477548544/0/880555-001-5_218x274_70.jpg",
        title:'耐克金品篮球鞋',
        des:"airjordan",
        price:'455',
        showList:true,
        list:[],
        goodsList:[],
        num:{},
        ch:true,
        select:true,
      }
    },
    computed: {
      ...mapGetters(['cartGoods']),
    },
    methods:{
      ...mapActions(['changeItem']),
      isChecked(inde){
        var change = JSON.parse(localStorage.getItem('mobile_goods')) ? JSON.parse(localStorage.getItem('mobile_goods')) : [];
        change.forEach(item=>{
          if(item.no == inde){
            if(item.selected == true){
              item.selected = false;
            }else{
              item.selected = true;
            }
            localStorage.setItem('mobile_goods',JSON.stringify(change));
            this.$store.dispatch('cartGoods',change);
            this.countPrice(inde);
          }
        })
      },
      countPrice(inde){
      },
      redirects(url) {
        this.$router.push(url);
      },
    },
    watch: {
    },
    beforeCreate(){
    },
    created(){   
      let list1 = [];
      this.$store.state.cartGoods.forEach(item=>{
        list1.push(item.no);
      });
      if(list1.length <= 0 ) return;
      // console.log(this.list)
          this.$axios.get(`/api/getCartGoods/${list1.join(',')}`).then((res)=>{
            if(res.data.err == 0){
              this.goodsList = res.data.data;
            }
          })
    }

  }
</script>

