<template>
  <div id="app">
    <Head v-show="$store.state.bHead"></Head>
    <router-view></router-view>
    <Foot v-show="$store.state.bFoot"></Foot>
  </div>
</template>

<script>
import Head from './components/head';
import Foot from './components/foot';
import { mapState } from 'vuex';
export default {
  name: 'app',
  components:{
    Head,Foot
  },
  computed:mapState(['bFoot','bHead']),
  watch:{
    $route:{
      handler({path}){
        // path = path.substr(1,path.length-1);
        // console.log(path)
        if(/goodsInfo|me/.test(path)){
          this.$store.dispatch('bHead',false)
          this.$store.dispatch('bFoot',true)
        }
        if(/community|home/.test(path)){
          this.$store.dispatch('bHead',true)
          this.$store.dispatch('bFoot',true)
          // console.log(this.$store.state.bHead)
        }
        if(/detail|login|register/.test(path)){
          this.$store.dispatch('bFoot',false)
          this.$store.dispatch('bHead',false)
        } 
        if(/cart/.test(path)){
          this.$store.dispatch('bFoot',false)
          this.$store.dispatch('bHead',true)
        } 
       },
       immediate:true
    }
  } 
}
</script>

